import { Component, EventEmitter, Input, Output } from '@angular/core';
import { ChatService } from '../../../../services/chat.service';
import firebase from 'firebase/app';
import 'firebase/firestore';
import { initializeFirebase } from 'src/app/chat/firebase.config';

@Component({
  selector: 'app-profilecards-other-users',
  templateUrl: './profilecards-other-users.component.html',
  styleUrls: ['./profilecards-other-users.component.scss'],
})
export class ProfilecardsOtherUsersComponent {
  private firestore: firebase.firestore.Firestore = firebase.firestore();
  @Input() currentUser!: string;
  @Input() memberData!: any;
  @Input() showProfileCard!: boolean;
  @Output() showProfileCardChild = new EventEmitter();
  @Input() showMembersDialogOpen!: boolean;
  @Output() showMembersDialogOpenChild = new EventEmitter();

  constructor(private chatService: ChatService) {
    initializeFirebase();
  }

  /**
   * @returns 
   */
  writeDirectMessage() {
    const q = this.firestore.collection(`users/${this.currentUser}/allDirectMessages`);
    return q.onSnapshot((list) => {
      let chatExists = false;
      list.forEach(element => {
        if (element.id === this.memberData.id) {
          chatExists = true;
          this.chatService.setSelectedUserId(this.memberData.id);
          this.closeProfileCard();
          this.closeShowMembers();
        }
      });
      if (!chatExists) {
        // Create new DM Chat
        this.addDirectMessage();
        this.closeProfileCard();
        this.closeShowMembers();
      }
    });
  }

  async addDirectMessage(): Promise<void> {
    const dmSenderRef = this.firestore.collection(`users/${this.currentUser}/allDirectMessages`).doc(this.memberData.id);
    const dmReceiverRef = this.firestore.collection(`users/${this.memberData.id}/allDirectMessages`).doc(this.currentUser);
    const data = {};
    await dmSenderRef.set(data);
    await dmReceiverRef.set(data);
    this.chatService.setSelectedUserId(this.memberData.id);
  }

  /**
   * Returns the active channel id that was selected.
   * @returns 
   */
  getActiveChannelId() {
    return this.chatService.getActiveChannelId();
  }

  /**
   * Closes the profilecard-other-users component.
   */
  closeProfileCard() {
    this.showProfileCard = false;
    this.showProfileCardChild.emit(this.showProfileCard);
  }

  /**
   * Closes the show-members-dialog component.
   */
  closeShowMembers() {
    this.showMembersDialogOpen = false;
    this.showMembersDialogOpenChild.emit(this.showProfileCard);
  }

  /**
   * Prevents an unwanted triggering of a function by clicking on an element.
   * @param $event 
   */
  doNotClose($event: any) {
    $event.stopPropagation();
  }
}