import { Component, EventEmitter, Output, OnInit, Input } from '@angular/core';
import firebase from 'firebase/app';
import 'firebase/firestore';
import { initializeFirebase } from 'src/app/chat/firebase.config';

initializeFirebase();

@Component({
  selector: 'app-add-members-dialog',
  templateUrl: './add-members-dialog.component.html',
  styleUrls: ['./add-members-dialog.component.scss'],
})
export class AddMembersDialogComponent implements OnInit {
  private firestore: firebase.firestore.Firestore = firebase.firestore();
  @Input() channelData!: any;
  @Input() currentChannelId: string;
  @Output() addMemberDialogOpenChild = new EventEmitter();
  addMemberDialogOpen: boolean;
  inputFocus = false;
  searchText = '';
  newUsersToAdd = [];
  userList = [];
  originalUserList: any;
  filteredUserList: any;
  @Input() addMembersMobileView!: boolean;

  ngOnInit(): void {
    this.getUsersToAdd();
  }

  getUsersToAdd() {
    const q = this.firestore.collection('users');
    return q.onSnapshot((list) => {
      list.forEach(element => {
        if (!this.channelData.members.includes(element.id)) {
          this.userList.push({
            'userName': element.data().name,
            'userId': element.id,
            'isOnline': element.data().isOnline,
            'imgUrl': element.data().imgUrl
          });
        }
      });
      this.filteredUserList = this.userList;
      this.originalUserList = this.userList;
    });
  }

  searchKey(data: string) {
    this.searchText = data;
    this.search();
  }

  search() {
    this.filteredUserList = this.userList;

    if (this.searchText !== "") {
      this.filteredUserList = this.userList.filter(user => {
        return user.userName.toLowerCase().includes(this.searchText.toLowerCase());
      });
    } else {
      this.filteredUserList = this.originalUserList;
    }
  }

  addUser(filteredUser: any) {
    const existingUser = this.userList.find(user => user.userId == filteredUser.userId);
    const indexOfAddedUser = this.userList.indexOf(existingUser);
    this.newUsersToAdd.push(filteredUser);
    this.userList.splice(indexOfAddedUser, 1);
    this.filteredUserList = this.userList;
    this.inputFocus = false;
  }

  removeAddedUser(userToAdd: any, i: number) {
    this.filteredUserList = this.userList;
    this.userList.push(userToAdd);
    this.newUsersToAdd.splice(i, 1);
    this.filteredUserList = this.userList;
    this.originalUserList = this.userList;
    this.inputFocus = false;
  }

  async addUsers() {
    for (let i = 0; i < this.newUsersToAdd.length; i++) {
      const user = this.newUsersToAdd[i]['userId'];
      if (!this.channelData.members.includes(user)) {
        this.channelData.members.push(user);
      }
    }
    const currentChannelRef = this.firestore.doc(`channels/${this.currentChannelId}`);
    const data = { members: this.channelData.members };
    await currentChannelRef.update(data);
    this.newUsersToAdd = [];
    this.closeDialog();
  }

  closeDialog() {
    this.addMemberDialogOpen = false;
    this.addMemberDialogOpenChild.emit(this.addMemberDialogOpen);
    this.inputFocus = false;
  }

  showUserList() {
    this.inputFocus = true;
  }

  closeUserList() {
    this.inputFocus = false;
  }

  doNotClose($event: any) {
    $event.stopPropagation();
  }
}