import { Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Router, NavigationStart } from '@angular/router';
import { FormBuilder, Validators, ValidationErrors } from '@angular/forms';
import firebase from 'firebase/app';
import 'firebase/auth';
import 'firebase/firestore';
import 'firebase/storage';
import { DirectMessage } from '../models/directMessage.class';
import { filter, takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { initializeFirebase } from '../firebase.config';
import { VariablesService } from 'src/app/_helpers/services/variables.service';

@Component({
  selector: 'chat-login',
  templateUrl: './login.component.html',
  styleUrls: [
    './login.component.scss',
    './login.component.animations.scss',
    './login.component.responsive.scss',
  ],
})
export class ChatLoginComponent implements OnInit, OnDestroy {
  auth = firebase.auth();
  db = firebase.firestore();
  storage = firebase.storage();

  startTextAnimation = false;
  removeDNone = false;
  removeAnimatedContainer = false;
  startLogoAnimation = false;
  isText = false;
  type = 'password';
  runAnimation = true;
  showAlert = false;
  showPasswordErrorDiv = false;
  showEmailErrorDiv = false;
  public getScreenWidth: any;
  public getScreenHeight: any;
  under650Px = false;
  addTheUpperMargin = false;
  alias: string;
  encodedAlias: string;
  hasAlias: boolean;
  signInForm: FormGroup;

  private destroy$ = new Subject<void>();

  constructor(private fb: FormBuilder, private router: Router, private variablesService: VariablesService) {
    initializeFirebase();
    const animationHasRunBefore = sessionStorage.getItem('animationHasRun');

    if (animationHasRunBefore) {
      this.runAnimation = false;
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  ngOnInit(): void {
    if (!Object.prototype.hasOwnProperty.call(this.variablesService.currentWallet.alias, 'name')) {
      this.hasAlias = false;
    } else {
      this.hasAlias = true;
      this.alias = this.variablesService.currentWallet.alias ? (this.variablesService.currentWallet.alias as any).name : '';
    }
    this.encodedAlias = encodeURIComponent(this.alias);
    this.getScreenHeight = window.innerHeight;
    this.getScreenWidth = window.innerWidth;
    if (this.getScreenWidth <= 650) {
      this.under650Px = true;
    } else if (this.getScreenWidth > 650) {
      this.under650Px = false;
    }

    if (this.getScreenHeight <= 1045) {
      this.addTheUpperMargin = true;
    } else if (this.getScreenHeight > 1045) {
      this.addTheUpperMargin = false;
    }

    sessionStorage.setItem('animationHasRun', 'true');

    this.router.events
      .pipe(
        filter((event) => event instanceof NavigationStart),
        takeUntil(this.destroy$)
      )
      .subscribe((event: NavigationStart) => {
        if (event.navigationTrigger === 'popstate') {
          this.runAnimation = false;
        }
      });

    this.shouldStartAnimation();

    this.signInForm = this.fb.group({
      email: [this.alias, [Validators.required]],
      password: [
        '',
        [Validators.required, Validators.minLength(8), Validators.maxLength(100)],
      ],
    });
  }

  navigateToSignUp(): void {
    this.router.navigate(['wallet/chat/chat-sign-up'], { queryParams: { alias: this.encodedAlias } });
  }

  @HostListener('window:resize', ['$event'])
  onWindowResize() {
    this.getScreenWidth = window.innerWidth;
    this.getScreenHeight = window.innerHeight;
    if (this.getScreenWidth <= 650) {
      this.under650Px = true;
    } else if (this.getScreenWidth > 650) {
      this.under650Px = false;
    }

    if (this.getScreenHeight <= 1045) {
      this.addTheUpperMargin = true;
    } else if (this.getScreenHeight > 1045) {
      this.addTheUpperMargin = false;
    }
  }

  shouldStartAnimation() {
    if (this.runAnimation == true) {
      this.startTheAnimation();
    } else {
      this.removeAnimatedContainer = true;
    }
  }

  startTheAnimation() {
    setTimeout(() => {
      this.startLogoAnimation = true;
    }, 1375);
    setTimeout(() => {
      this.removeDNone = true;
    }, 1500);
    setTimeout(() => {
      this.startTextAnimation = true;
    }, 1500);
    setTimeout(() => {
      this.removeAnimatedContainer = true;
    }, 3100);
  }

  async createUserDetailsDoc(result) {
    await this.db.collection('users').doc(result.user.uid).set({
      name: result.user.displayName,
      email: result.user.email,
      imgUrl: result.user.photoURL,
      isOnline: false,
      id: result.user.uid,
    });
  }

  async signIn() {
    await this.auth.signInWithEmailAndPassword(
      'nirmata' + this.signInForm.value.email + '.com',
      this.signInForm.value.password
    )
      .then(async () => {
        const userRef = this.db.collection('users').doc(this.auth.currentUser.uid);
        await userRef.update({
          isOnline: true,
        });
        this.router.navigate(['/wallet/chat/']);
      })
      .catch((error) => {
        console.error('Error signing in:', error);
        this.showFalseLoginAlert();
      });
  }

  async signInAsGuest() {
    await this.auth.signInWithEmailAndPassword(
      'guest@mail.com',
      'Guest12345678'
    );
    this.router.navigateByUrl('/chat');
  }

  goToPasswordReset() {
    this.router.navigateByUrl('/wallet/chat/chat-reset-password');
  }

  togglePasswordVisibility() {
    this.isText = !this.isText;
    this.type = this.isText ? 'text' : 'password';
  }

  showFalseLoginAlert() {
    this.showAlert = true;
    setTimeout(() => {
      this.signInForm.reset();
      this.showPasswordErrorDiv = false;
      this.showEmailErrorDiv = false;
      this.isText = false;
      this.showAlert = false;
    }, 2000);
  }

  getEmailErrorMessage(errors: ValidationErrors) {
    if (errors['required']) {
      return 'Please enter your e-mail address';
    } else if (errors['pattern']) {
      return 'No valid e-mail address';
    } else {
      return;
    }
  }

  getPWErrorMessage(errors: ValidationErrors) {
    if (errors['required']) {
      return 'Please enter your password';
    } else if (errors['minlength']) {
      return 'The password must be longer than 8 characters';
    } else if (errors['maxlength']) {
      return 'The password cannot be longer than 100 characters';
    } else {
      return;
    }
  }

  checkErrors(control: string) {
    const errors = (this.signInForm.controls as any)[control].errors;
    if (control === 'email') {
      return this.getEmailErrorMessage(errors);
    } else if (control === 'password') {
      return this.getPWErrorMessage(errors);
    }
  }
}