import {Component, OnInit, OnDestroy, Input} from '@angular/core';
import { VariablesService } from 'src/app/_helpers/services/variables.service';
import { BackendService } from 'src/app/_helpers/services/backend.service';
import { IntToMoneyPipe } from 'src/app/_helpers/pipes/int-to-money.pipe';
import { BLOCK_EXPLORER_TX_URL_PREFIX } from 'src/app/_shared/constants';
import { BLOCK_EXPLORER_TN_TX_URL_PREFIX } from 'src/app/_shared/constants';
import { DeeplinkParams, p2pFormData } from 'src/app/_helpers/models/wallet.model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-offers-details',
  templateUrl: './offers-details.component.html',
  styleUrls: ['./offers-details.component.scss']
})
export class OffersDetailsComponent implements OnInit, OnDestroy {

  @Input() offerDetails: any;
  @Input() sizes: any;
  actionData: DeeplinkParams = {};

  constructor(public variablesService: VariablesService, private _router: Router, private backendService: BackendService, private intToMoneyPipe: IntToMoneyPipe) {}

  ngOnInit() {// Initialization logic here
  }

  openInBrowser(tr) {
    this.backendService.openUrlInBrowser((this.variablesService.testnet ? BLOCK_EXPLORER_TN_TX_URL_PREFIX : BLOCK_EXPLORER_TX_URL_PREFIX)+ tr);
  }

  nextStep(): void {
    this.variablesService.sendActionData$.next(this.actionData);
    this.variablesService.deeplink$.next(null);
    this.fillData()
    this._router.navigate(['/wallet/contracts/purchase']).then();
  }

  fillData() {
    const formData: p2pFormData = {
      description: this.offerDetails.do,
      seller_address: this.offerDetails.url,
      amount: this.offerDetails.lco,
      my_deposit: this.offerDetails.my_deposit || '',
      seller_deposit: this.offerDetails.seller_deposit || '',
      comment: this.offerDetails.pt,
      contract_address: this.offerDetails.payment_type
    };
  
    this.variablesService.purchaseFormData.next(formData);
  }

  copyToClipboard(divId: string): void {
    const divElement = document.getElementById(divId) as HTMLDivElement;
    if (divElement && divElement.innerText) {
      this.backendService.setClipboard(divElement.innerText);}
    }

  ngOnDestroy() {// Initialization logic here}
  }
}
