import { Component, HostListener, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, ValidationErrors, Validators } from '@angular/forms';
import firebase from 'firebase/app';
import 'firebase/auth';
import { Router } from '@angular/router';
import { CustomValidators } from '../models/custom-validators';
import { initializeFirebase } from 'src/app/chat/firebase.config';

const auth = firebase.auth();

@Component({
  selector: 'app-password-reset',
  templateUrl: './password-reset.component.html',
  styleUrls: [
    './password-reset.component.scss',
    './password-reset.responsive.scss',
  ],
})
export class PasswordResetComponent implements OnInit {
  constructor(
    private router: Router,
    private fb: FormBuilder
  ) {
    initializeFirebase();
  }

  passwordResetForm!: FormGroup;
  mode = '';
  authCode = '';
  first = true;
  second = false;
  emailSent = false;
  passwordChanged = false;
  sendEmailBtnDisabled = false;
  resetPWBtnDisabled = false;
  showEmailErrorDiv = false;
  showPasswordErrorDiv = false;
  showConfirmPasswordErrorDiv = false;
  isText1 = false;
  isText2 = false;
  type1 = 'password';
  type2 = 'password';
  public getScreenWidth: any;
  public getScreenHeight: any;
  addTheUpperMargin = false;

  emailForPasswordResetForm = this.fb.group({
    email: ['', [Validators.required, Validators.email]],
  });

  ngOnInit(): void {
    document.addEventListener('DOMContentLoaded', () => {
      this.mode = this.getParameterByName('mode');
      this.authCode = this.getParameterByName('oobCode');
      this.switchView();
    });
    this.passwordResetForm = this.fb.group(
      {
        password: [
          '',
          [
            Validators.required,
            Validators.minLength(8),
            Validators.maxLength(100),
          ],
        ],
        confirmPassword: [
          '',
          [
            Validators.required,
            Validators.minLength(8),
            Validators.maxLength(100),
          ],
        ],
      },
      {
        validators: CustomValidators.passwordsMatching,
      }
    );

    this.getScreenHeight = window.innerHeight;

    if (this.getScreenHeight <= 951) {
      this.addTheUpperMargin = true;
    } else if (this.getScreenHeight > 951) {
      this.addTheUpperMargin = false;
    }
  }

  @HostListener('window:resize', ['$event'])
  onWindowResize() {
    this.getScreenHeight = window.innerHeight;

    if (this.getScreenHeight <= 951) {
      this.addTheUpperMargin = true;
    } else if (this.getScreenHeight > 951) {
      this.addTheUpperMargin = false;
    }
  }

  getParameterByName(name) {
    name = name.replace(/[[]/, '[').replace(/[\]]/, ']');
    const regexS = '[\\?&]' + name + '=([^&#]*)';
    const regex = new RegExp(regexS);
    const results = regex.exec(window.location.href);
    if (results == null) return '';
    else return decodeURIComponent(results[1].replace(/\+/g, ' '));
  }

  switchView() {
    if (this.mode === 'resetPassword') {
      this.second = true;
      this.first = false;
    } else {
      this.second = false;
      this.first = true;
    }
  }

  goBackToLogin() {
    this.router.navigateByUrl('chat-login');
  }

  async sendEmail() {
    auth.sendPasswordResetEmail(this.emailForPasswordResetForm.value.email);
    this.sendEmailBtnDisabled = true;
    this.emailSent = true;
    setTimeout(() => {
      this.emailSent = false;
      this.sendEmailBtnDisabled = false;
      this.goBackToLogin();
    }, 1500);
  }

  checkPasswordErrors(control: string) {
    const errors = (this.passwordResetForm.controls as any)[control].errors;
    return this.getPWErrorMessage(errors);
  }

  checkEmailErrors(control: string) {
    const errors = (this.emailForPasswordResetForm.controls as any)[control]
      .errors;
    return this.getEmailErrorMessage(errors);
  }

  getPWErrorMessage(errors: ValidationErrors) {
    if (errors['required']) {
      return 'Bitte geben Sie einen Passwort ein';
    } else if (errors['minlength']) {
      return 'Das Passwort muss lnger als 8 Zeichen sein';
    } else if (errors['maxlength']) {
      return 'Das Passwort darf nicht lnger als 100 Zeichen sein';
    } else {
      return;
    }
  }

  getEmailErrorMessage(errors: ValidationErrors) {
    if (errors['required']) {
      return 'Bitte geben Sie Ihre E-Mail-Adresse ein';
    } else if (errors['pattern']) {
      return 'Keine gltige E-Mail-Adresse';
    } else {
      return;
    }
  }

  resetPassword() {
    this.handleResetPassword(auth, this.authCode);
  }

  handleResetPassword(auth, actionCode) {
    auth.verifyPasswordResetCode(actionCode).then(() => {
      const newPassword = this.passwordResetForm.value.confirmPassword;
      auth.confirmPasswordReset(actionCode, newPassword)
        .then(() => {
          this.passwordChanged = true;
          setTimeout(() => {
            this.passwordChanged = false;
            this.goBackToLogin();
          }, 2000);
        })
        .catch((error) => {
          console.log(error.message);
        });
    });
  }

  togglePassword1Visibility() {
    this.isText1 = !this.isText1;
    this.type1 = this.isText1 ? 'text' : 'password';
  }

  togglePassword2Visibility() {
    this.isText2 = !this.isText2;
    this.type2 = this.isText2 ? 'text' : 'password';
  }
}