import { Component, OnInit, NgZone, ElementRef, ViewChild } from '@angular/core';
import { BackendService } from '../_helpers/services/backend.service';
import { DeeplinkParams, PushOffer, CancelOffer, Wallet, UpdateOffer } from './../_helpers/models/wallet.model';
import { MIXIN } from '../_shared/constants';
import { BigNumber } from 'bignumber.js';
import { VariablesService } from '../_helpers/services/variables.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute } from '@angular/router';
import { ModalService } from '../_helpers/services/modal.service';

@Component({
  selector: 'app-swap',
  templateUrl: './swap.component.html',
  styleUrls: ['./swap.component.scss']
})

export class SwapComponent implements OnInit {
  @ViewChild('head') head: ElementRef;
  hasAlias = true;
  offerForm: FormGroup;
  cancelOfferForm: FormGroup;
  updateOfferForm: FormGroup;
  secondStep = false;
  actionData: DeeplinkParams = {}
  marketplace_offer_send = false;
  marketplace_offer_create = false;
  marketplace_offer_cancel = false;
  marketplace_offer_update = false;
  marketplace_offer_update_send = false;
  sendRoute = false;
  defaultMixin = MIXIN;
  data:any = {};
  offers:any = [];
  offersDetails:any = [];
  total_offers = 0;
  calculatedWidth = [];
  walletsTopay: Array<Wallet> = [];
  openedDetails = '';
  parentRouting;
  filteredOffers: any = [];
  filter: any = {};
  alias: '';
  optionsToOffer: any = {};
  confirmedContractNumber: number;
  thisWalletOffers: any = [];
  confirmedTransactions = true;
  checkOffers = false;

  constructor(private backend: BackendService, private ngZone: NgZone, public variablesService: VariablesService, private fb: FormBuilder, private http: HttpClient, private route: ActivatedRoute, private modalService: ModalService) {

   }

  ngOnInit() {
    if (!Object.prototype.hasOwnProperty.call(this.variablesService.currentWallet.alias, 'name')) {
      this.hasAlias = false;
    } else {
      this.alias = this.variablesService.currentWallet.alias ? (this.variablesService.currentWallet.alias as any).name : '';
    }
    this.getOffers();
    this.parentRouting = this.route.parent.params.subscribe(() => {
      this.openedDetails = '';
    });
    this.offerForm = this.fb.group({
      title: ['Buy', Validators.required],
      alias: [this.alias, Validators.required],
      category: ['NIR', Validators.required],
      description: ['', Validators.required],
      amount: ['0.01', Validators.required],
      payment_type: ['', Validators.required],
      price: ['', Validators.required],
      contact: ['', Validators.required],
      comment: ['USDT', Validators.required],
    }, { validators: this.checkAllFieldsFilled });
    this.cancelOfferForm = this.fb.group({
      tx_id: ['', Validators.required],
      no: [0, Validators.required]
    }, { validators: this.checkAllFieldsFilled });
    this.updateOfferForm = this.fb.group({
      tx_id: ['', Validators.required],
      no: [0, Validators.required],
      title: ['Buy', Validators.required],
      alias: [this.alias, Validators.required],
      category: ['NIR', Validators.required],
      description: ['', Validators.required],
      amount: ['0.01', Validators.required],
      payment_type: ['', Validators.required],
      price: ['', Validators.required],
      contact: ['', Validators.required],
      comment: ['USDT', Validators.required],
    }, { validators: this.checkAllFieldsFilled });

    this.optionsToOffer = {
      crypto: [
        { value: 'NIR', label: 'NIR' },
        { value: 'XMR', label: 'XMR' },
        { value: 'BTC', label: 'BTC' },
        { value: 'ETH', label: 'ETH' },
        { value: 'LTC', label: 'LTC' },
        { value: 'KAS', label: 'KAS' },
        { value: 'TON', label: 'TON' },
        { value: 'DOGE', label: 'DOGE' },
        /*{ value: 'RVN', label: 'RVN' },*/
        { value: 'BNB', label: 'BNB' },
        { value: 'ETC', label: 'ETC' },
        /*{ value: 'DASH', label: 'DASH' },*/
        { value: 'USDT', label: 'USDT' },
        { value: 'USDC', label: 'USDC' },
      ],
      fiat: [
        { value: '$(USD)', label: '$(USD)' },
        { value: '(EURO)', label: '(EURO)' },
        { value: '.(AED)', label: '.(AED)' },
        { value: '(JPY)', label: '(JPY)' },
        { value: '(TRY)', label: '(TRY)' },
        { value: '(RUB)', label: '(RUB)' },
        /*{ value: '(KZT)', label: '(KZT)' },*/
        { value: '(GBP)', label: '(GBP)' },
        { value: '(CNY)', label: '(CNY)' },
        { value: '(INR)', label: '(INR)' },
        { value: '$(TWD)', label: '$(TWD)' },
        { value: '$(SGD)', label: '$(SGD)' },
        { value: '$(CAD)', label: '$(CAD)' },
      ]
    };
  }

//--------------------------------------------GET OFFERS--------------------------------------------
async getOffers() {
  /*const urls = ["http://87.251.79.161:11113/json_rpc",
                "http://87.251.79.162:11113/json_rpc",
                "http://localhost:11113/json_rpc"
              ];*/
  const urls = ["http://194.246.114.23:11232/json_rpc",
              "http://103.113.70.162:11232/json_rpc",
              "http://85.21.233.202:11232/json_rpc",
              "http://45.14.247.211:11232/json_rpc"
            ];
  const shuffledUrls = urls.sort(() => 0.5 - Math.random());

  for (const url of shuffledUrls) {
    try {
      const res = await this.http.post(url, {
        jsonrpc: "2.0",
        id: "0",
        method: "marketplace_global_get_offers_ex",
        params: {
          filter: {
            amount_low_limit: 0,
            amount_up_limit: 0,
            bonus: false,
            category: "",
            fake: false,
            keyword: "",
            limit: 1000,
            location_city: "",
            location_country: "",
            offer_type_mask: 0,
            offset: 0,
            order_by: 0,
            primary: "",
            rate_low_limit: "0.000000",
            rate_up_limit: "0.000000",
            reverse: true,
            target: "",
            timestamp_start: 0,
            timestamp_stop: 0,
          },
        },
      }).toPromise();
      this.data = res;
      if (this.data.result.offers) {
        this.offers = this.data.result.offers;
        this.total_offers = this.data.result.total_offers;
        this.filteredOffers = [...this.offers];
        this.thisWalletOffers = this.offers.filter(offer => offer.url.includes(this.alias));
        this.checkOffers = false;
        break;
      } else {
        this.checkOffers = true;
      }
    } catch (err) {
      console.error(`Fetch error from ${url}`, err);
    }
  }
}
//--------------------------------------------CREATE OFFER--------------------------------------------
  marketplaceSend(): void {
    const offerObject: PushOffer = {
      wallet_id: this.variablesService.currentWallet.wallet_id,
      od: {
        ap: '',
        at: this.confirmedContractNumber.toString(),
        cat: this.actionData.category,
        cnt: this.actionData.contact,
        com: this.actionData.comment,
        do: this.actionData.description,
        et: 3,
        fee: new BigNumber(
          '' +
            (+this.actionData.fee || +this.variablesService.default_fee) *
              1000000000000
        ),
        lci: this.actionData.price,
        lco: this.actionData.amount,
        ot: 1,
        pt: this.actionData.payment_type,
        t: this.actionData.title,
        url: this.actionData.url,
      },
    };
    this.backend.push_offer(offerObject, (status, data) => {
      this.ngZone.run(() => {
        if (data.success) {
          this.modalService.prepareModal('success', 'Offer added to the blockchain!');
          this.toggleConfirmedTransactions();
        } else {
          this.modalService.prepareModal('error', 'Offer creation error!');
          this.canselAction();
        }
      });
    });
    this.marketplace_offer_send = false;
  }

  submitOffer(): void {
    this.marketplace_offer_create = false;
    if (this.offerForm.valid) {
      const formData = this.offerForm.value;
      this.actionData.url = formData.alias;
      this.actionData.amount = formData.amount;
      this.actionData.payment_type = formData.payment_type;
      this.actionData.price = formData.price;
      this.actionData.title = formData.title;
      this.actionData.category = formData.category;
      this.actionData.description = formData.description;
      this.actionData.contact = formData.contact;
      this.actionData.comment = formData.comment;
    }
  }
//--------------------------------------------UPDATE OFFER--------------------------------------------
  submitUpdateOffer(): void {
    this.marketplace_offer_update = false;
    if (this.updateOfferForm.valid) {
      const formData = this.updateOfferForm.value;
      this.actionData.tx_id = formData.tx_id;
      this.actionData.no = formData.no;
      this.actionData.url = formData.alias;
      this.actionData.amount = formData.amount;
      this.actionData.payment_type = formData.payment_type;
      this.actionData.price = formData.price;
      this.actionData.title = formData.title;
      this.actionData.category = formData.category;
      this.actionData.description = formData.description;
      this.actionData.contact = formData.contact;
      this.actionData.comment = formData.comment;
    }
  }

  marketplaceUpdate(): void {
    const offerObject: UpdateOffer = {
      wallet_id: this.variablesService.currentWallet.wallet_id,
      id: this.actionData.tx_id,
      io: this.actionData.no,
      of: {
        ap: '',
        at: this.confirmedContractNumber.toString(),
        cat: this.actionData.category,
        cnt: this.actionData.contact,
        com: this.actionData.comment,
        do: this.actionData.description,
        et: 3,
        fee: new BigNumber(
          '' +
            (+this.actionData.fee || +this.variablesService.default_fee) *
              1000000000000
        ),
        lci: this.actionData.price,
        lco: this.actionData.amount,
        ot: 1,
        pt: this.actionData.payment_type,
        t: this.actionData.title,
        url: this.actionData.url,
      },
    };
    this.backend.update_offer(offerObject, (status, data) => {
      this.ngZone.run(() => {
        if (data.success) {
          this.modalService.prepareModal('success', 'Offer updated successfully!');
        } else {
          this.modalService.prepareModal('error', 'Offer update error!');
          this.canselAction();
        }
      });
    });
    this.marketplace_offer_update_send = false;
  }
//--------------------------------------------CANCEL OFFER--------------------------------------------
  marketplaceCancelSend() {
    this.marketplace_offer_cancel = false;
    if (this.cancelOfferForm.valid) {
      const formData = this.cancelOfferForm.value;
      this.actionData.tx_id = formData.tx_id;
      this.actionData.no = formData.no;
    }
    this.cancelOffer();
  }

  cancelOffer(): void { 
    const offerObject: CancelOffer = {
        wallet_id: this.variablesService.currentWallet.wallet_id,
        id: this.actionData.tx_id,
        io: this.actionData.no
    };
    this.backend.cancel_offer(offerObject, (status, data) => {
        if (data.success) {
          this.modalService.prepareModal('success', 'Offer cancelled successfully!');
        } else {
          this.modalService.prepareModal('error', 'Cancellation error!');
          this.canselAction();
        }
    });
  }
//------------------------------------------------------------------------------------------------------
  checkAllFieldsFilled(group: FormGroup) {
    let isFilled = true;
    Object.keys(group.controls).forEach(controlName => {
      const control = group.get(controlName);
      if (control.value === '' || control.value === null) {
        isFilled = false;
      }
    });
    return isFilled ? null : { fieldsNotFilled: true };
  }

  validateAndSubmit() {
    if (this.offerForm.valid && this.marketplace_offer_create) {
      this.marketplace_offer_send = true;
    } else {
      if (this.marketplace_offer_create) {
        this.modalService.prepareModal('error', 'All fields must be filled in!');
      }
    }

    if (this.cancelOfferForm.valid && this.marketplace_offer_cancel) {
      this.marketplaceCancelSend();
    } else {
      if (this.marketplace_offer_cancel) {
        this.modalService.prepareModal('error', 'All fields must be filled in!');
      }
    }

    if (this.updateOfferForm.valid && this.marketplace_offer_update) {
      this.marketplace_offer_update_send = true;
    } else {
      if (this.marketplace_offer_update) {
        this.modalService.prepareModal('error', 'All fields must be filled in!');
      }
    }
  }

  applyFilters() {
    if (!this.filter.category && !this.filter.price) {
      this.filteredOffers = [...this.offers];
    } else {
      this.filteredOffers = this.offers.filter(offer => {
        const matchesCategory = this.filter.category ? offer.cat.toLowerCase().includes(this.filter.category.toLowerCase()) : true;
        const matchesComment = this.filter.category ? offer.com.toLowerCase().includes(this.filter.category.toLowerCase()) : true;
        return matchesCategory || matchesComment;
      });
    }
  }

  canselAction(): void {
    this.variablesService.deeplink$.next(null)
    this.variablesService.sendActionData$.next({});
    this.actionData = {};
    this.secondStep = false;
    this.marketplace_offer_send = false;
  }

  openDetails(tx_hash) {
    if (tx_hash === this.openedDetails) {
      this.openedDetails = '';
      this.offersDetails = [];
    } else {
      this.openedDetails = tx_hash;
      this.offersDetails = this.offers.find(offer => offer.tx_hash === this.openedDetails);
    }
  }

  numberOnly(event): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if ((charCode >= 48 && charCode <= 57) || charCode == 46 || charCode == 44) {
      const currentValue = event.target.value;
      if ((charCode == 46 || charCode == 44) && (currentValue.indexOf('.') > -1 || currentValue.indexOf(',') > -1)) {
        return false;
      }
      return true;
    } else {
      return false; 
    }
  }

  confirmedContract() {
    this.backend.getContracts(this.variablesService.currentWallet.wallet_id, (status, data) => {
      if (data && data.contracts) {
        this.confirmedContractNumber = data.contracts.length;
      } else {
        this.confirmedContractNumber = 0;
      }
    });
  }

  openInBrowser(link) {
    this.backend.openUrlInBrowser(link);
  }

  toggleConfirmedTransactions() {
    this.confirmedTransactions = false;
    setTimeout(() => {
      this.confirmedTransactions = true;
    }, 60000);
  }

  refreshOffers() {
    this.getOffers();
    this.modalService.prepareModal('success', 'List Offers Updated!');
    this.checkOffers = false;
  }

  calculateWidth() {
    this.calculatedWidth = [];
    this.calculatedWidth.push(this.head.nativeElement.childNodes[0].clientWidth);
    this.calculatedWidth.push(this.head.nativeElement.childNodes[1].clientWidth + this.head.nativeElement.childNodes[2].clientWidth);
    this.calculatedWidth.push(this.head.nativeElement.childNodes[3].clientWidth);
    this.calculatedWidth.push(this.head.nativeElement.childNodes[4].clientWidth);
  }

}
