import { Component, ElementRef, HostListener, OnInit, ViewChild, ChangeDetectorRef, NgZone, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { ActivatedRoute } from '@angular/router';
import firebase from 'firebase/app';
import { FormGroup } from '@angular/forms';
import 'firebase/auth';
import 'firebase/firestore';
import 'firebase/storage';
import { FormBuilder, ValidationErrors, Validators } from '@angular/forms';
import { DirectMessage } from '../models/directMessage.class';
import { initializeFirebase } from 'src/app/chat/firebase.config';

@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.scss', './sign-up.component.responsive.scss'],
})
export class ChatSignUpComponent implements OnInit, OnDestroy {
  auth = firebase.auth();
  db = firebase.firestore();
  storage = firebase.storage();

  first = true;
  second = false;
  person = 'zero';
  current: Date = new Date();
  uID = '';
  userCreationSuccess = false;
  isText = false;
  type = 'password';
  showNameAndUsernameErrorDiv = false;
  showEmailErrorDiv = false;
  showPasswordErrorDiv = false;
  showCheckboxErrorDiv = false;
  public getScreenWidth: any;
  public getScreenHeight: any;
  addTheUpperMargin = false;
  alias: string;
  registerForm: FormGroup;

  genericImg = 'assets/img/login/profile_generic_big.png';
  person1Img = 'assets/img/userImages/userImage1.svg';
  person2Img = 'assets/img/userImages/userImage2.svg';
  person3Img = 'assets/img/userImages/userImage3.svg';
  person4Img = 'assets/img/userImages/userImage4.svg';
  person5Img = 'assets/img/userImages/userImage5.svg';
  person6Img = 'assets/img/userImages/userImage6.svg';
  imgUrl = this.genericImg;

  @ViewChild('fileUpload') fileUpload: ElementRef;
  filePath: string;
  isCustomImage = false;

  private isDestroyed = false;
  variablesService: any;

  constructor(private route: ActivatedRoute, private router: Router, private fb: FormBuilder, private cdr: ChangeDetectorRef, private ngZone: NgZone) {
    initializeFirebase();
  }

  ngOnInit(): void {
    this.route.queryParams.subscribe(params => {
      this.alias = params['alias'] ? decodeURIComponent(params['alias']) : '';
    });
    console.log('alias sign-up', this.alias);
    this.getScreenHeight = window.innerHeight;

    if (this.getScreenHeight <= 1045) {
      this.addTheUpperMargin = true;
    } else if (this.getScreenHeight > 1045) {
      this.addTheUpperMargin = false;
    }
    this.registerForm = this.fb.group({
      nameAndSurname: [this.alias, Validators.required],
      //email: [this.alias, [Validators.required]],
      password: [
        '',
        [Validators.required, Validators.minLength(8), Validators.maxLength(100)],
      ]
    });
  }

  ngOnDestroy(): void {
    this.isDestroyed = true;
  }

  @HostListener('window:resize', ['$event'])
  onWindowResize() {
    this.getScreenHeight = window.innerHeight;

    if (this.getScreenHeight <= 1045) {
      this.addTheUpperMargin = true;
    } else if (this.getScreenHeight > 1045) {
      this.addTheUpperMargin = false;
    }
  }

  goBackToLogin() {
    this.router.navigate(['/wallet/chat/chat-login']);
  }

  goBackToFirst() {
    this.first = true;
    this.second = false;
  }

  getNotesRef() {
    return this.db.collection('users');
  }

  goToAvatarChoice() {
    this.first = false;
    this.second = true;
  }

  chooseAvatar(person: number) {
    if (this.isCustomImage) {
      this.deleteImageFromStorage(); // Lscht das aktuelle Bild aus dem Storage
      this.isCustomImage = false; // Setze isCustomImage zurck, da wir jetzt einen Standardavatar verwenden
    }

    if (person == 1) {
      this.imgUrl = this.person1Img;
      this.person = this.person1Img;
    } else if (person == 2) {
      this.imgUrl = this.person2Img;
      this.person = this.person2Img;
    } else if (person == 3) {
      this.imgUrl = this.person3Img;
      this.person = this.person3Img;
    } else if (person == 4) {
      this.imgUrl = this.person4Img;
      this.person = this.person4Img;
    } else if (person == 5) {
      this.imgUrl = this.person5Img;
      this.person = this.person5Img;
    } else if (person == 6) {
      this.imgUrl = this.person6Img;
      this.person = this.person6Img;
    }
  }

  async signUp() {
    await this.auth.createUserWithEmailAndPassword(
      'nirmata' + this.registerForm.value.nameAndSurname + '.com',
      this.registerForm.value.password
    );
    await this.auth.currentUser.updateProfile({
      displayName: this.registerForm.value.nameAndSurname,
      photoURL: this.imgUrl,
    });
    await this.createUserDetailsDoc();
    await this.addUserToGeneralChannel();
    this.animateAndGoBackToLogin();
  }

  async createUserDetailsDoc() {
    await this.db.collection('users').doc(this.auth.currentUser.uid).set({
      name: this.auth.currentUser.displayName,
      email: this.auth.currentUser.email,
      imgUrl: this.auth.currentUser.photoURL,
      isOnline: false,
      id: this.auth.currentUser.uid,
    });
  }

  async addUserToGeneralChannel() {
    const channelRef = this.db.collection('channels').doc('LBu5OmUNMHvzbfrYEDGZ');
    const channelRefA = this.db.collection('channels').doc('N785mg3NB8dbKHJg1DYC9jHqwF13');
    const channelDoc = await channelRef.get();
    const channelDocA = await channelRefA.get();
    if (channelDoc.exists) {
      await channelRef.update({
        members: firebase.firestore.FieldValue.arrayUnion(this.auth.currentUser.uid),
      });
    } else {
      console.error('Channel document does not exist');
    }
    if (channelDocA.exists) {
      await channelRefA.update({
        members: firebase.firestore.FieldValue.arrayUnion(this.auth.currentUser.uid),
      });
    } else {
      console.error('Channel document does not exist');
    }
  }

  animateAndGoBackToLogin() {
    this.userCreationSuccess = true;
    setTimeout(() => {
      this.userCreationSuccess = false;
      this.auth.signOut();
      this.router.navigateByUrl('wallet/chat/chat-login');
    }, 2000);
  }

  async onFileSelected(event) {
    const file: File = event.target.files[0];
    if (!file) return;
    const validTypes = ['image/png', 'image/jpeg', 'image/svg+xml'];
    if (!validTypes.includes(file.type)) {
      alert('Only PNG, JPG and SVG files are allowed.');
      return;
    }
    const maxSizeInBytes = 1.5 * 1024 * 1024; // 1,5 MB in Bytes
    if (file.size > maxSizeInBytes) {
      alert('The file is too large. Maximum file size is 1,5 MB.');
      return;
    }
    await this.uploadImage(file);
  }

  generateUniqueId() {
    return (
      Math.random().toString(36).substring(2, 15) +
      Math.random().toString(36).substring(2, 15)
    );
  }

  async uploadImage(file: File) {
    try {
      const uniqueId = this.generateUniqueId();
      const uniqueFileName = `${uniqueId}-${file.name}`;
      const filePath = `profileImages/${uniqueFileName}`;
      this.filePath = filePath;
      const storageRef = this.storage.ref(filePath);
      const uploadTask = await storageRef.put(file);
      const downloadUrl = await uploadTask.ref.getDownloadURL();
      this.isCustomImage = true;
      this.imgUrl = downloadUrl; // Setze die neue Bild-URL

      // Ensure changes are detected
      this.ngZone.run(() => {
        if (!this.isDestroyed) {
          this.cdr.detectChanges();
        }
      });
    } catch (error) {
      console.error('Error uploading file: ', error);
    }
  }

  async removeFileUpload() {
    if (!this.filePath) return;
    try {
      const storageRef = this.storage.ref(this.filePath);
      await storageRef.delete();
      this.imgUrl = this.genericImg; // Setze das Bild zurck auf das Standardbild
      this.resetFileInput();
      this.isCustomImage = false;

      // Ensure changes are detected
      this.ngZone.run(() => {
        if (!this.isDestroyed) {
          this.cdr.detectChanges();
        }
      });
    } catch (error) {
      console.error('Error deleting file: ', error);
    }
  }

  private resetFileInput() {
    if (this.fileUpload && this.fileUpload.nativeElement) {
      this.fileUpload.nativeElement.value = '';
    }
  }

  async deleteImageFromStorage() {
    if (!this.filePath || !this.isCustomImage) return;
    try {
      const storageRef = this.storage.ref(this.filePath);
      await storageRef.delete();
      this.resetFileInput(); // Optional: Setze das file input zurck
      // Beachte, dass wir imgUrl hier nicht ndern
    } catch (error) {
      console.error('Fehler beim Lschen des Bildes aus dem Storage: ', error);
    }
  }

  togglePasswordVisibility() {
    this.isText = !this.isText;
    this.type = this.isText ? 'text' : 'password';
  }

  getPWErrorMessage(errors: ValidationErrors) {
    if (errors['required']) {
      return 'Please enter a password';
    } else if (errors['minlength']) {
      return 'The password must be longer than 8 characters';
    } else if (errors['maxlength']) {
      return 'The password cannot be longer than 100 characters';
    } else {
      return;
    }
  }

  getNameAndSurnameErrorMessage(errors: ValidationErrors) {
    if (errors['required']) {
      return 'Please enter your name and surname';
    } else {
      return;
    }
  }

  getEmailErrorMessage(errors: ValidationErrors) {
    if (errors['required']) {
      return 'Please enter your e-mail address';
    } else if (errors['pattern']) {
      return 'No valid e-mail address';
    } else {
      return;
    }
  }

  getCheckBoxErrorMessage(errors: ValidationErrors) {
    if (errors['required']) {
      return 'You must activate the checkbox to continue';
    } else {
      return;
    }
  }

  checkErrors(control: string) {
    const errors = (this.registerForm.controls as any)[control].errors;
    if (control === 'password') {
      return this.getPWErrorMessage(errors);
    } else if (control === 'email') {
      return this.getEmailErrorMessage(errors);
    } else if (control === 'nameAndSurname') {
      return this.getNameAndSurnameErrorMessage(errors);
    } else if (control === 'checkbox') {
      return this.getCheckBoxErrorMessage(errors);
    }
  }
}